/** @jsx jsx */
import { jsx } from 'theme-ui'
import { useState } from 'react'
import UnderlineInput, { UnderlineTextarea } from './UnderlineInput'
import { CheckCircleIcon } from './Icons'

function ContactForm() {
  const [isSent, setIsSent] = useState(false)

  function submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        setIsSent(true);
      } else {
        setIsSent(false);
      }
    };
    xhr.send(data);
  }
  return (
    <div sx={{ mt: '6rem', mx: 'auto', maxWidth: '30rem' }} id="form-section">
      <h1 sx={{ fontSize: 80, textAlign: 'center', mb: "6rem" }}>Let's chat</h1>
      <div>
        <form
          action="https://formspree.io/hello@bitmatica.com"
          method="POST"
          onSubmit={submitForm}
        >
          <UnderlineInput
            placeholder="Your name"
            type="text"
            name="name"
            id="name"
            required={true}
          />
          <UnderlineInput
            placeholder="Your email"
            name="email"
            type="email"
            id="email"
            required={true}
          />
          <UnderlineInput
            placeholder="Your phone"
            name="phone"
            type="tel"
            id="phone"
            required={true}
          />
          <UnderlineTextarea
            placeholder="Your message"
            name="message"
            id="message"
            required={true}
          />
          {isSent && (
            <div>
              <div
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  mb: '1rem',
                  p: '1rem',
                  bg: 'deeppink',
                  color: 'white',
                }}
              >
                <CheckCircleIcon fill="white" />{' '}
                <div sx={{ ml: '.5rem', fontWeight: 600 }}>
                  Message sent. We'll reach out soon.
                </div>
              </div>
            </div>
          )}
          <button
            sx={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              width: '100%',
              p: '1rem',
              bg: '#5A06E3',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            <div sx={{ width: '100%', textAlign: 'center' }}>
              <div sx={{ fontWeight: 600 }}>Send</div>
            </div>
          </button>
        </form>
      </div>
    </div>
  )
}

export default ContactForm
