/** @jsx jsx */
import { jsx } from 'theme-ui'
import { useState } from 'react'
import styled, { keyframes } from 'styled-components'
import TextareaAutosize from 'react-autosize-textarea'

const fadeInUp = keyframes`
  from {
    opacity: 0;
    transform: translate3d(0, 50%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
`

const Label = styled.div`
  position: absolute;
  top: .5rem;
  left: 1rem;
  z-index: 1;
  opacity: 0;
  animation: ${fadeInUp} 0.5s forwards;
  font-size: 14px;
  font-weight: 600;
  color: rgba(250,250,250, .4);
  text-transform: capitalize;
`

export function UnderlineInput(props) {
  const [isFocused, setIsFocused] = useState(false)
  const [value, setValue] = useState('')
  const showLabel = isFocused || Boolean(value)

  return (
    <div sx={{ position: "relative" }}>
      {showLabel && <Label>{props.name}</Label>}
      <input
        onChange={e => setValue(e.target.value)}
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
        sx={{
          display: 'block',
          width: '100%',
          border: 0,
          mb: '1rem',
          fontSize: '1rem',
          background: 'rgba(255, 255, 255, 0.10)',
          padding: showLabel ? '2rem 1rem 1rem 1rem' : "1rem",
          lineHeight: 1.5,
          transition: "all .3s",
          color: 'white',
          '::placeholder': {
            color: 'rgba(255, 255, 255, 0.6)',
          },
          ':focus::-webkit-input-placeholder': { color: 'transparent' },
        }}
        {...props}
      />
    </div>
  )
}

// TODO: Factor out repeated code across UnderlineInput and UnderlineTextarea.
export function UnderlineTextarea(props) {
  const [isFocused, setIsFocused] = useState(false)
  const [value, setValue] = useState('')
  const showLabel = isFocused || Boolean(value)

  return (
    <div sx={{ position: "relative" }}>
      {showLabel && <Label>{props.name}</Label>}
      <TextareaAutosize
        onChange={e => setValue(e.target.value)}
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
        sx={{
          display: 'block',
          width: '100%',
          border: 0,
          mb: '1rem',
          fontSize: '1rem',
          background: 'rgba(255, 255, 255, 0.10)',
          padding: showLabel ? '2rem 1rem 1rem 1rem' : '1rem',
          lineHeight: 1.5,
          transition: 'all .3s',
          color: 'white',
          resize: "none",
          minHeight: 120,
          '::placeholder': {
            color: 'rgba(255, 255, 255, 0.6)',
          },
          ':focus::-webkit-input-placeholder': { color: 'transparent' },
        }}
        {...props}
      />
    </div>
  )
}

export default UnderlineInput
